.container-maker {
  display: flex;
  flex-direction: column;
  padding: 1vh;
  border: 2px solid rgb(0, 0, 0);
  background-color: aliceblue;
  width: 100%;
  height: 100%;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  overflow: hidden;
}

.card-title {
  font-size: larger;
}

.card-img-top {
  height: 180px;
}

.card-text {
  border: 1px solid black;
  padding: 1vh;
  font-size: 0.6em;
  font-style: italic;
  text-align: center;
  word-wrap: break-word;
  width: 100%;
}

.card-body {
  width: 100%;
  padding: 1vh;
}

.card-body-atrb {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 1vh;
}

.card-body-atrb p {
  font-size: 0.7em;
}

.card-atrib {
  text-align: center;
  width: 14vw;
  color: white;
  background-color: black;
  border-radius: 15px;
}

.classification {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.classification p {
  font-size: 0.8em;
}

.card-atrib-trunf {
  text-align: center;
  text-transform: uppercase;
  border-radius: 15px;
  background-color: rgb(247, 96, 9);
  width: fit-content;
  padding: 5px;
  color: white;
}

.card-atrib-class {
  text-align: center;
  text-transform: uppercase;
  border-radius: 15px 0 15px 0;
  background-color: black;
  width: fit-content;
  padding: 5px;
  margin-top: 4vh;
  color: white;
}

@media screen and ( min-width : 768px ) {
  
  .card-body-atrb p {
    font-size: 0.8em;
  }
}

@media screen and ( min-width : 1024px ) {
  
  .card-title {
    font-size: 1.7em;
  }

  .card-img-top {
    height: 120px;
  }

  .card-text {
    font-size: 0.7em;
    text-align: justify;
  }

  .card-body-atrb {
    margin-top: 1.5vh;
  }

  .card-body-atrb p {
    font-size: 0.9em;
  }

  .card-atrib {
    width: 5vw;
  }

  .classification p {
    margin-top: 7vh;
  }
}

@media screen and ( min-width : 1440px ) {
  
  .card-img-top {
    height: 180px;
  }

  .card-title {
    font-size: 1.9em;
    padding: 1vh;
  }

  .card-text {
    font-size: 1em;
    text-align: justify;
  }

  .card-body-atrb p {
    font-size: 1.3em;
  }

  .card-atrib {
    width: 6vw;
  }

  .classification p {
    font-size: 1.3em;
  }
}

